@import url('https://fonts.googleapis.com/css?family=Quicksand');

/* Global Styles */
:root {
    --primary-color: #003699;
    --dark-color: #333333;
    --light-color: #999;
    --danger-color: #dc3545;
    --success-color: #28a745;
}

i {
    color:white;
}


a {
    color: var(--primary-color);
    text-decoration: none;
}

a:hover {
    color: #666;
}

ul {
    list-style: none;
}

img {
    width: 100%;
}
.logo {
    width: 85%;
  }
/* Utilities */
.container {
    max-width: 100%;
    margin: auto;
    overflow: hidden;
    padding: 0 1rem 1rem 1rem;
}

/* Text Styles*/
.x-large {
    font-size: 4rem;
    line-height: 1.2;
    margin-bottom: 1rem;
}

.large {
    font-size: 3rem;
    line-height: 1.2;
    margin-bottom: 1rem;
}

.lead {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.text-center {
    text-align: center;
}

.text-primary {
    color: var(--primary-color);
}
.text-white {
    color: var(--light-color);
}
.text-dark {
    color: var(--dark-color);
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

.text-left {
    text-align: left;
}
/* Grid */
.mainContainer{
    display: flex;
    
    aling-items:center;
    
  }
  .smMargin {
    margin: 1rem;
  }
  .actionDiv {
    text-align: center;
  }
/* Cards */
.card {
    padding: .4rem;
    box-shadow: inset;
    margin: 0.7rem 0;
    border-radius: 6px;
}
.card-header {
    background: #6d3d6d;
  border-radius: 4px;
  padding: .4rem;
  font-size: 1.5rem;
}
.card-body {
margin: .2rem;
  padding: .1rem;
}

/* List */
.list {
    margin: 0.5rem 0;
}

.list li {
    padding-bottom: 0.3rem;
}

/* Padding */
.p {
    padding: 0.5rem;
}
.p-1 {
    padding: 1rem;
}
.p-2 {
    padding: 2rem;
}
.p-3 {
    padding: 3rem;
}
.py {
    padding: 0.5rem 0;
}
.py-1 {
    padding: 1rem 0;
}
.py-2 {
    padding: 2rem 0;
}
.py-3 {
    padding: 3rem 0;
}

/* Margin */
.m {
    margin: 0.5rem;
}
.m-1 {
    margin: 1rem;
}
.m-2 {
    margin: 2rem;
}
.m-3 {
    margin: 3rem;
}
.my {
    margin: 0.5rem 0;
}
.my-1 {
    margin: 1rem 0;
}
.my-2 {
    margin: 2rem 0;
}
.my-3 {
    margin: 3rem 0;
}

/* Grid */
.grid-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
}

.grid-3 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
}

.grid-4 {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 2rem;
}

.grid-2-3 {
    display: grid;
    grid-template-columns: 2fr 3fr;
    grid-gap: 2rem;
}

.btn {
    display: inline-block;
    background: #6d3d6d;
    color: #333;
    padding: 0.4rem 1.3rem;
    font-size: 1rem;
    border: none;
    cursor: pointer;
    margin-right: 0.5rem;
    transition: opacity 0.2s ease-in;
    outline: none;
}

.btn-link {
    background: none;
    padding: 0;
    margin: 0;
}

.btn-block {
    display: block;
    width: 100%;
}

.btn-sm {
    font-size: 0.8rem;
    padding: 0.3rem 1rem;
    margin-right: 0.2rem;
}

.badge {
    font-size: 0.8rem;
    padding: 0.2rem 0.7rem;
    text-align: center;
    margin: 0.3rem;
    background: var(--light-color);
    color: #333;
    border-radius: 5px;
}

.alert {
    padding: 0.7rem;
    margin: 1rem 0;
    opacity: 0.9;
    background: var(--light-color);
    color: #333;
}

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
    background: #6d3d6d;
    color: #fff;
}
.btn-primary:hover,
.bg-primary:hover,
.badge-primmary:hover,
.alert-primary:hover{
    background: var(--light-color);
    color: #000000;
}


.btn-light,
.bg-light,
.badge-light,
.alert-light {
    background: var(--light-color);
    color: #222;
}

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
    background: var(--dark-color);
    color: #fff;
}

.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
    background: var(--danger-color);
    color: #fff;
}

.btn-success,
.bg-success,
.badge-success,
.alert-success {
    background: var(--success-color);
    color: #fff;
}

.btn-white,
.bg-white,
.badge-white,
.alert-white {
    background: #fff;
    color: #222;
    font-weight: bolder;
    border: #ccc solid 1px;
}
.btn-white:hover,
.bg-white:hover,
.badge-white:hover,
.btn:hover,
.alert-white :hover{
font: inherit;
    color:#ddd;
    background: #222;
    opacity: 0.7;
}


.bg-light,
.badge-light {
    border: #ccc solid 1px;
}

.round-img {
    border-radius: 50%;
}

/* Forms */
input {
    margin: 1rem 0;
}
label {
    color: #fff;
  }
.form-text {
    display: block;
    margin-top: 0.3rem;
    color: #888;
}

input[type='text'],
input[type='email'],
input[type='password'],
input[type='date'],
input[type='file'],
input[type='textarea'],
select,
textarea {
    display: block;
    width: 100%; 
    padding: 1rem;
    font-size: 1.3rem;
    color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
    background: #3c1f3c;
}

input[type='submit'],
button {
    font: inherit;
 
}
input[type='submit']:hover,
button {
    font: inherit;
    color:#ddd;
    background: #222;
  
 
}
.form-group {
    margin: 1.2rem 0;
}

.form-group input {
    margin: 0.2rem 0;
   
}

.form-container {
    max-width: 500px;
    margin: 1rem auto;
    overflow: hidden;
    border: #ccc 1px dotted;
    padding: 0 1.5rem;
    background: #6d3d6d;
}

.form-container h1,
h2,
h3 {
    text-align: center;
    color:#ddd;
    font-weight: lighter;
    background: #4c2a4c;
    border-radius: 4px;
    padding: 5px;
    margin-top: .5rem;
}

table th,
table td {
    padding: 1rem;
    text-align: left;
}

table th {
    background: var(--light-color);
}


/* Animation (Add After) */
.item-enter {
    opacity: 0;
}
.item-enter-active {
    opacity: 1;
    transition: opacity 1000ms ease-in;
}
.item-exit {
    opacity: 1;
}
.item-exit-active {
    opacity: 0;
    transition: opacity 1000ms ease-in;
}

/* Mobile Styles */
@media (max-width: 700px) {
    .hide-sm {
        display: none;
    }

    .grid-2,
    .grid-3,
    .grid-4 {
        grid-template-columns: 1fr;
    }

    /* Text Styles */
    .x-large {
        font-size: 3rem;
    }

    .large {
        font-size: 2rem;
    }

    .lead {
        font-size: 1rem;
    }

   
}
