@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Quicksand', sans-serif;
}
html {
  height: 100%;
}
body {
 
  font-size: 1rem;
  line-height: 1.6;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #553055;
  color:#eee;
}
.App{

  background: #4c2a4c;
  margin: 20px auto;
  max-width: 95%;
  color: #eee;
  align-items: center;
  
}



form{
  padding: 20px;
}

input[type='text']{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  background: #3c1f3c;
  color: #fff;
  border: 0;
}
input[type='submit']{
  margin: 10px auto;
  background: #999;
  border: 0;
  padding: 6px 20px;
  display: block;
}
.bg-transparent {
  background-color:  rgba(32, 176, 0, 0.1);
}
